@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-regular.woff2") format("woff2"), url("../fonts/lato/Lato-regular.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-header {
  background-color: #ffffff;
  color: #000000;
  margin: 0 auto;
  width: 320px;
}
.main-header__container {
  width: 100%;
  box-sizing: border-box;
  padding-top: 13px;
}
.main-header__logo {
  display: block;
  cursor: pointer;
  outline: none;
}
.main-header__logo--name {
  margin-left: 56px;
}
.main-header__logo:hover,
.main-header__logo:focus {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.6;
}
.main-header__logo-container {
  display: flex;
  max-width: 320px;
  padding: 0 20px 14px 20px;
  box-sizing: border-box;
  align-items: center;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main-header__container {
    margin: 0 auto;
    display: flex;
    padding-top: 25px;
    width: 704px;
  }
  .main-header {
    width: auto;
    min-width: 704px;
  }
  .main-header__logo--name {
    margin-left: 13px;
    margin-right: 91px;
  }
  .main-header__logo-container {
    margin: 0;
    padding: 0;
  }
}
@media (min-width: 1440px) {
  .main-header {
    padding-top: 30px;
    padding-left: 110px;
    padding-right: 110px;
    width: 1220px;
    position: relative;
    margin: 0 auto;
  }
  .main-header--index::before {
    content: "";
    display: block;
    width: 720px;
    height: 694px;
    background-color: #68b738;
    opacity: 85%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
  }
  .main-header--index::after {
    content: "";
    display: block;
    width: 720px;
    height: 694px;
    background-image: url("../img/cat-tongue-desktop.jpg");
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
  }
  .main-header__container {
    justify-content: space-between;
    margin: 0;
    width: 1220px;
  }
  .main-header__logo-container {
    display: flex;
    width: 202px;
    justify-content: space-between;
    align-items: center;
    padding: 0;
  }
  .main-header__logo--name {
    display: none;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .main-header--index::after {
    background-image: url("../img/cat-tongue-desktop@2x.jpg");
    background-size: cover;
  }
}
.main-nav {
  position: relative;
}
.main-nav__toggle {
  border: none;
  display: block;
  height: 24px;
  width: 24px;
  position: absolute;
  top: -44px;
  right: 21px;
  cursor: pointer;
  outline: none;
}
.main-nav__toggle:hover,
.main-nav__toggle:focus {
  opacity: 0.8;
}
.main-nav__toggle:active {
  opacity: 0.6;
}
.main-nav--closed .main-nav__toggle {
  background-image: url("../img/header-menu.svg");
  background-repeat: no-repeat;
  background-color: transparent;
}
.main-nav--opened .main-nav__toggle {
  background-image: url("../img/menu-exit.svg");
  background-repeat: no-repeat;
  background-color: transparent;
}
.main-nav--opened .main-nav__wrapper {
  position: absolute;
  width: 100%;
  z-index: 2;
}
.main-nav--closed .main-nav__wrapper {
  display: none;
}
/*
.menu-open {
  display: none;
}*/
@media (min-width: 768px) {
  .main-nav--closed .main-nav__wrapper,
  .main-nav--opened .main-nav__wrapper {
    display: flex;
    position: relative;
  }
  .main-nav--closed .main-nav__toggle,
  .main-nav--opened .main-nav__toggle {
    display: none;
  }
  .menu-open {
    display: flex;
  }
  .main-nav {
    position: relative;
    z-index: 3;
    padding-top: 8px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    padding-right: 0;
  }
}
